<template>
	<view class="container">
		<view class="registerInfo">
			<u-form :model="form" ref="uForm" :label-width="180">
				<view class="info_box">
					<u-form-item label="婚姻状况">
						<view class="flexBet">
							<ld-select :underlist="marryList" list-key="label" value-key="value" placeholder="请选择婚姻状况" v-model="form.marryState"
							 @change="marryChange"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
						<!-- <picker @change="marryChange" :value="form.marryState" :range="marryList">
							<view class="flexBet">
								<input type="text" disabled="true" placeholder="请选择婚姻状况" :value="marryList[form.marryState]" placeholder-class="phcolor" />
								<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
							</view>
						</picker> -->
					</u-form-item>
					<u-form-item label="意向级别">
						<!-- <picker @change="selectIntenLeve($event,intenLeveList)" :value='intenIndex' :range="intenLeveList" :range-key="'label'">
							<view class="flexBet">
								<input type="text" disabled="true" placeholder="请选择意向级别" :value="intenLeveList[intenIndex].label" placeholder-class="phcolor" />
								<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
							</view>
						</picker> -->
						<view class="flexBet">
							<ld-select :underlist="intenLeveList" list-key="label" value-key="value" placeholder="请选择意向级别" v-model="form.intensionLevel"
							 @change="selectIntenLeve"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</u-form-item>
					<u-form-item label="添加分组">
						<view class="flexBet">
							<ld-select :underlist="groupList" list-key="label" value-key="value" placeholder="请选择分组" v-model="groupIds"
							 :multiple="true" @change="selectGroup"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</u-form-item>
					<!-- <u-form-item label="关联客户" style="display: none;">
						<picker @change="customChange" :value="customIndex" :range="customList">
							<view class="flexBet">
								<input type="text" disabled="true" placeholder="请选择客户来源" :value="customList[customIndex]" placeholder-class="phcolor" />
								<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
							</view>
						</picker>
					</u-form-item> -->
					<!-- <u-form-item label="跟进内容">
						<u-input v-model="form.followUpContent" type="textarea" placeholder="请填写备注" />
					</u-form-item> -->
					<view class="followWrap">
						<text>跟进内容</text>
						<u-input v-model="form.followUpContent" type="textarea" placeholder="请填写备注" />
					</view>
					<u-form-item label="下次跟进">
						<dateTime @getDate="_getDate"></dateTime>
					</u-form-item>
					<u-form-item label="认知途径">
						<view class="flexBet">
							<ld-select :underlist="cognitiveList" list-key="label" value-key="value" placeholder="请选择认知途径" v-model="form.cognitiveApproach"
							 @change="selectCognitive"></ld-select>
							<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
						</view>
					</u-form-item>
						<!-- <picker @change="selectCognitive($event,cognitiveList)" :value="cognitiveIndex" :range="cognitiveList" :range-key="'label'">
							<view class="flexBet">
								<input type="text" disabled="true" placeholder="请选择认知途径" :value="cognitiveList[cognitiveIndex].label" placeholder-class="phcolor" />
								<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
							</view>
						</picker>
					</u-form-item> -->
				</view>
			</u-form>
		</view>
		<view class="footer_flex">
			<text @click="goBack">上一步</text>
			<text @click="registerTap">立即登记</text>
		</view>
	</view>
</template>

<script>
	import dateTime from '@/components/dateTime.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				form: {},
				groupList:[], // 分组列表
				intenLeveList:[], // 意向级别列表
				cognitiveList:[], // 认知途径列表
				groupIds:[], // 分组id
				marryList:[{
					label:'未婚',
					value:'0'
				},{
					label:'已婚',
					value:'1'
				},{
					label:'保密',
					value:'2'
				}]
				// intenIndex:'', // 意向级别索引
				// cognitiveIndex:'', // 认知途径索引
				// marryList: ['未婚', '已婚', '保密'], // 婚姻状态list
			}
		},
		onLoad(options){
			this.form = JSON.parse(decodeURIComponent(options.form))
			this._getList()
		},
		methods: {
			_getList(){
				Tools.showLoading('加载中')
				let that = this
				httpUtils.sendHttpRequest('2021', Object.assign({}, ''), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.groupList = res.data.data.groups
						that.intenLeveList = res.data.data.intentionLevels
						that.cognitiveList = res.data.data.cognitiveApproachs
					}
				})
			},
			// 日期回调
			_getDate(data){
				this.form.nextFollowTime = data
			},
			// 婚姻状况选择
			marryChange(val) {
				this.form.marryState = val
			},
			// 意向级别选择
			selectIntenLeve(val) {
				this.form.intensionLevel = val
			},
			// 分组选择
			selectGroup(val) {
				this.groupIds = val
				this.form.groupId = this.groupIds
			},
			// 认知途径选择
			selectCognitive(val) {
				this.form.cognitiveApproach = val
			},
			// 完善信息
			goBack(){
				uni.navigateBack({
					delta:1
				})
			},
			// 立即登记
			registerTap(){
				Tools.showLoading('提交中')
				httpUtils.sendHttpRequest('2020', Object.assign({}, this.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('登记成功')
						setTimeout(function() {
							uni.reLaunch({
								url:'/pages/salemanVisual/customer/index'
							})
						}, 500)
					}
				})
			}
		},
		components:{
			dateTime
		}
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';
	page {
		background-color: #f6f6f6;
		.info_box{
			background-color: #fff;
			padding: 0 30rpx;
		}
	}
</style>
